:root {
  font-family: "Inter", sans-serif;
}
@supports (font-variation-settings: normal) {
  :root {
    font-family: "Inter var", sans-serif;
  }
}

.header {
  position: sticky;
  grid-area: h;
  /* background-color: red; */
  top: 0;
  padding: 0;
  background: rgba(0, 0, 0, 0.8);
  display: grid;
  /* justify-content: center; */
}

.content {
  height: 50px;
  /* background-color: red; */
  display: grid;
  align-content: center;
  grid-template-columns: 50% 50%;
  width: 100%;
  margin: 0 auto;
  max-width: 1200px;
  /* justify-content: center; */
}

.logo {
  width: 92px;
  height: 30px;
  /* background-color: aqua; */
  display: grid;
  align-content: center;
  grid-template-columns: 9px 83px;
}

.firstBlock {
  display: grid;
  width: 9px;
  height: 30px;
  /* background-color: blue; */
  align-content: center;
  grid-template-columns: 2px 5px;
  column-gap: 2px;
}

.firstBox {
  display: grid;
  height: 30px;
  width: 2px;
  border-radius: 40px;
  background-color: #c1a875;
}

.secondBox {
  display: grid;
  height: 20px;
  width: 5px;
  border-radius: 40px;
  background-color: #3a73a8;
}

.secondBlock {
  display: grid;
  /* background-color: green; */
  grid-template-rows: 20px 10px;
}

.firstText {
  display: grid;
  background-color: yellow;
  font-family: inter;
  font-size: 25px;
  font-weight: 400;
  line-height: 30px;
  letter-spacing: 0em;
  text-align: left;
  margin: 0;
  margin-top: -5px;
  background: linear-gradient(0deg, #c1a875, #c1a875),
    linear-gradient(
      88.77deg,
      #aabbbb -21.83%,
      #a8aead 12.44%,
      #9ea8a6 53.6%,
      #757a7b 91.4%,
      rgba(45, 46, 46, 0) 117.15%
    );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  background-blend-mode: color, normal;
}

.secondText {
  display: grid;
  /* background-color: orange; */
  font-family: Inter;
  font-size: 9px;
  font-weight: 400;
  line-height: 8px;
  letter-spacing: 0em;
  text-align: left;
  color: #dadada;
  margin-left: 2px;
}

.nav {
  display: grid;
  grid-template-columns: 50px 70px 57px 70px;
  column-gap: 12%;
  align-content: center;
  justify-content: end;
  margin-left: 50%;
  /* background-color: green; */
}

.nav a {
  font-size: 13px;
  font-family: Inter;
  text-decoration: none;
  color: #dadada;
}
