.content {
  grid-area: c;
  background-color: #1e1e1e;
  color: white;
  /* margin: 0 8px; */
  display: grid;
  grid-template-rows: 10px 1fr 500px;
}

.lights {
  display: grid;

  /* margin-top: 10px; */
}

.goldLight {
  display: grid;
  width: 100%;
  height: 5px;
  background: #ffd276;
  box-shadow: 0px 0px 50px #ffd276, 0px 0px 25px #ffd276, 0px 0px 10px #ffd276;
  filter: blur(0.5px);
}

