.text {
  display: grid;
  justify-content: center;
  font-family: "Courier";
  font-style: normal;
  font-weight: 400;
  font-size: 25px;
  line-height: 29px;
  /* identical to box height */
  color: #07ba03;
  text-shadow: 0px 0px 25px #07ba03, 0px 0px 5px #07ba03;
  filter: blur(0.5px);
}

.block {
  display: grid;
  background-color: #151515;
  margin: 20px;
  border-radius: 20px;
}
